import { Response } from '@angular/http';
import { VisionService } from './../../core/services/vision/vision.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-visionapi',
  templateUrl: './visionAPI.component.html',
  styleUrls: ['./visionAPI.component.css']
})
export class VisionAPIComponent implements OnInit {
  imageData: {} = {};
  constructor(
    private visionService: VisionService
  ) { }

  ngOnInit() {
  }

  imageUploaded($event: Event) {
    this.imageData = $event['serverResponse'].json()[0];
    console.log($event);
    console.log(this.imageData);
  }
}



