// Angular
import { NgModule, Optional, SkipSelf } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {
  MdButtonModule,
  MdCheckboxModule,
  MdInputModule,
  MdCardModule,
  MdRadioModule,
  MdSidenavModule,
  MdToolbarModule,
  MdListModule,
  MdGridListModule,
  MdIconModule,
  MdProgressSpinnerModule,
  MdProgressBarModule,
  MdTabsModule,
  MdSlideToggleModule,
  MdButtonToggleModule,
  MdSliderModule,
  MdMenuModule,
  MdTooltipModule,
  MdDialogModule,
  MdSnackBarModule,
  MdSelectModule
} from '@angular/material';

import { throwIfAlreadyLoaded } from './guards/module-import-guard';
import './rxjs-extensions';

import { VisionService } from './services/vision/vision.service';

@NgModule({
  imports: [
    CommonModule,
    FlexLayoutModule,
    FormsModule,
    RouterModule,
    HttpModule,
    BrowserModule,
    BrowserAnimationsModule,
    MdButtonModule,
    MdCheckboxModule,
    MdInputModule,
    MdCardModule,
    MdRadioModule,
    MdSidenavModule,
    MdToolbarModule,
    MdListModule,
    MdGridListModule,
    MdIconModule,
    MdProgressSpinnerModule,
    MdProgressBarModule,
    MdTabsModule,
    MdSlideToggleModule,
    MdButtonToggleModule,
    MdSliderModule,
    MdMenuModule,
    MdTooltipModule,
    MdDialogModule,
    MdSnackBarModule,
    MdSelectModule
  ],
  exports: [
    CommonModule,
    FlexLayoutModule,
    FormsModule,
    RouterModule,
    HttpModule,
    BrowserModule,
    BrowserAnimationsModule,
    MdButtonModule,
    MdCheckboxModule,
    MdInputModule,
    MdCardModule,
    MdRadioModule,
    MdSidenavModule,
    MdToolbarModule,
    MdListModule,
    MdGridListModule,
    MdIconModule,
    MdProgressSpinnerModule,
    MdProgressBarModule,
    MdTabsModule,
    MdSlideToggleModule,
    MdButtonToggleModule,
    MdSliderModule,
    MdMenuModule,
    MdTooltipModule,
    MdDialogModule,
    MdSnackBarModule,
    MdSelectModule
  ],
  declarations: [
  ],
  providers: [
    VisionService
  ]
})
export class CoreModule {
  constructor( @Optional() @SkipSelf() parentModule: CoreModule) {
    throwIfAlreadyLoaded(parentModule, 'CoreModule');
  }
}



